import router from '../router'

	export default{
		name:'Search',
		data(){
			return{

				lists:['道康宁','安全鞋','有机硅密','耳塞','角磨机','钻头','瞬干胶','魔力克','口罩','田岛工具']
			}
		},
		methods:{
			handlelist(item){
				var list = JSON.stringify(item)
				localStorage.setItem('name',list)
				this.$router.push({path:'/footer/list'})
			},
			handlefh(){
				router.go(-1)
			}
		}
	}



